import { isoDate } from '@speedlo/graphql'
import { Sentry } from '@speedlo/sentry'
import { Instance, SnapshotIn, types } from 'mobx-state-tree'
import { VError } from 'verror'

import {
  DeliveryTypeEnum,
  MSubmitOrderVariables,
  PaymentGateEnum,
  QCartRecipeDocument,
  QFoodMenuDocument,
} from '../graph/generated'
import {
  GCreateDeliveryOrder,
  GCreateOrder,
  GCreatePickupOrder,
  GVamStatus,
} from '../graph/types'
import { logMain } from '../helpers/logger'
import { RM } from '../helpers/ramda'
import { i18n } from '../i18n'
import {
  PickupMapBranchQuery,
  PickupMapBrandQuery,
} from '../segments/map/PickupMap.graphql'
import { IOrderFormState } from '../segments/orderForm/OrderFormSetup'
import { BaseModel } from './BaseModel'
import {
  CreateDeliveryOrderMutation,
  CreatePickupOrderMutation,
  VamStatusQuery,
} from './OrderModel.graphql'

interface IVolatileProps {
  isSubmitting: boolean
  isGoPayChecking: boolean
  submitErrors: Maybe<ReadonlyArray<string>>
  agreements: TAgreement[]
  lastActiveRecipeId: MaybeID
  isVamInitialized: boolean
  timePickerOpen: boolean
  delayedOrderEnabled: boolean
  addToCartRecipeId: MaybeID
  changeCartItemId: MaybeID
}

export type TAgreement = GVamStatus.VamStatus

export const OrderModel = BaseModel.named('Order')
  .props({
    branchId: types.maybeNull(types.string),
    orderId: types.maybeNull(types.string),
    orderMethod: DeliveryTypeEnum.Messenger,
    payment: PaymentGateEnum.Gopay,
    deliverAt: types.maybe(types.Date),
    firstname: '',
    lastname: '',
    email: '',
    phone: '',
    phonePrefix: '',
    note: '',
    tips: '',
  })
  .volatile<IVolatileProps>(self => ({
    isSubmitting: false,
    isGoPayChecking: false,
    submitErrors: null,
    paymentErrors: null,
    agreements: [],
    lastActiveRecipeId: null,
    isVamInitialized: false,
    timePickerOpen: false,
    delayedOrderEnabled: false,
    addToCartRecipeId: null,
    changeCartItemId: null,
  }))
  .views(self => ({
    get hasOrder() {
      return self.orderId !== null
    },
    get usingDeliveryOrderMethod() {
      return self.orderMethod === DeliveryTypeEnum.Messenger
    },
    get usingPickupOrderMethod() {
      return self.orderMethod === DeliveryTypeEnum.Pickup
    },
    get usingPaymentMethodWithTips() {
      return self.payment === PaymentGateEnum.Gopay
    },
    get hasErrors() {
      return Boolean(self.submitErrors && self.submitErrors.length > 0)
    },
    get defaultPhonePrefix() {
      if (self.root.business.isValid && self.root.business.country) {
        return self.root.business.country.phonePrefix
      }
      return '+'
    },
    getSubmitVariables(formValues: IOrderFormState): MSubmitOrderVariables {
      return {
        orderId: self.orderId!,
        customer: {
          firstName: formValues.firstname,
          lastName: formValues.lastname,
          emails: [{ email: formValues.email }],
          phones: [{ phone: `${formValues.phonePrefix}${formValues.phone}` }],
        },
        note: formValues.note,
        tips: formValues.tips.length ? Number.parseInt(formValues.tips, 10) : 0,
        paymentGate: formValues.payment,
        deliverAt: self.deliverAt && isoDate(self.deliverAt),
        agreements: formValues.agreements
          .filter(RM.propEq('isConfirmed', true))
          .map(RM.prop('id'))
          .filter(Boolean),
      }
    },
  }))
  .actions(self => ({
    useOrderMethod(method: DeliveryTypeEnum) {
      self.orderMethod = method
    },
    setPaymentMethod(method: PaymentGateEnum) {
      self.payment = method
    },
    setBranch(branchId: ID) {
      self.branchId = branchId
    },
    orderCreated(result: GCreateOrder.Fragment) {
      self.orderId = result.id
      self.branchId = result.companyBranch.id
      self.deliverAt = undefined
      logMain('order %s created for branch %s', self.orderId, self.branchId)
    },
    orderPaid(orderId: ID) {
      if (self.orderId !== orderId) {
        self.orderId = orderId
        logMain('order %s replaced by %s due to successful payment status')
      }
    },
    orderCancel() {
      if (self.branchId || self.orderId) {
        logMain(
          'cancel order %s, was at branch %s',
          self.root.order.orderId,
          self.root.order.branchId,
        )
      }
      self.orderId = null
      self.branchId = null
    },
    submitFinished() {
      self.isSubmitting = false
    },
    submitFailed(errors?: ReadonlyArray<string>) {
      self.isSubmitting = false
      self.submitErrors = errors || [
        i18n.t`Error during sending order. Please try again later.`,
      ]
    },
    vamFailed() {
      self.submitErrors = [
        i18n.t`Internal error during sending order. Order can not be finished right now. Our team was notified and is working on fix. Thank you for your understanding.`,
      ]
      self.isVamInitialized = false
    },
    clearErrors() {
      self.submitErrors = null
    },
    goPayStatusCheck(checking: boolean) {
      self.isGoPayChecking = checking
    },
    useAgreements(agreements: ReadonlyArray<TAgreement>) {
      if (Array.isArray(agreements)) {
        self.agreements = agreements
      }
      self.isVamInitialized = true
    },
    setUsedRecipeId(recipeId: ID) {
      self.lastActiveRecipeId = recipeId
    },
    openTimePicker() {
      self.timePickerOpen = true
    },
    closeTimePicker() {
      self.timePickerOpen = false
    },
    setTips(tips: string) {
      self.tips = tips
    },
    setDeliverAt(deliverAt: Date | undefined) {
      self.deliverAt = deliverAt
    },
    enableDelayedOrder() {
      self.delayedOrderEnabled = true
    },
    addToCart(recipeId: MaybeID) {
      self.addToCartRecipeId = recipeId
    },
    changeCartItem(itemId: MaybeID) {
      self.changeCartItemId = itemId
    },
    rememberFormValues(values: IOrderFormState) {
      if (self.root.user.shouldRememberForm) {
        self.firstname = values.firstname
        self.lastname = values.lastname
        self.email = values.email
        self.phone = values.phone
        self.phonePrefix = values.phonePrefix
        self.note = values.note
        self.tips = values.tips
        self.payment = values.payment
      } else {
        self.firstname = ''
        self.lastname = ''
        self.email = ''
        self.phone = ''
        self.phonePrefix = ''
        self.note = ''
        self.tips = ''
        self.payment = PaymentGateEnum.Gopay
      }
    },
  }))
  .actions(self => ({
    async createDeliveryOrder() {
      const { business, location } = self.root
      if (!location.geoPoint) {
        return null
      }
      const variables: GCreateDeliveryOrder.Variables = {
        gps: location.geoPoint,
        orderOrigin: business.orderOrigin,
        ...business.brandOrBranch,
      }
      const { data } = await self.client.mutate({
        fetchPolicy: 'no-cache',
        mutation: CreateDeliveryOrderMutation,
        variables,
      })
      const { order } = data as GCreateDeliveryOrder.Mutation
      self.orderCreated(order)
      // @ts-ignore method defined lower
      self.initVam()
      return self.orderId
    },
    async createPickupOrder(branchId: ID) {
      const { business } = self.root
      const variables: GCreatePickupOrder.Variables = {
        branchId,
        orderOrigin: business.orderOrigin,
      }
      const { data } = await self.client.mutate({
        fetchPolicy: 'no-cache',
        mutation: CreatePickupOrderMutation,
        variables,
      })
      const { order } = data as GCreatePickupOrder.Mutation
      self.orderCreated(order)
      // @ts-ignore method defined lower
      self.initVam()
      return self.orderId
    },
    prefetchPickup() {
      const { business } = self.root
      if (business.isBrand) {
        self.client.query({
          query: PickupMapBrandQuery,
          variables: { brandId: business.brandId },
        })
      } else {
        self.client.query({
          query: PickupMapBranchQuery,
          variables: { branchId: business.branchId },
        })
      }
    },
    prefetchFoodMenu(branchId?: ID) {
      if (branchId) {
        self.client.query({ query: QFoodMenuDocument, variables: { branchId } })
      }
    },
    prefetchFood(recipeId: ID) {
      self.client.query({
        query: QCartRecipeDocument,
        variables: { recipeId },
      })
    },
    async initVam() {
      const variables: GVamStatus.Variables = {
        input: {
          orderId: self.orderId!,
          email: self.email,
          firstname: self.firstname,
          lastname: self.lastname,
          phone: self.phone,
        },
      }
      try {
        const result = await self.client.query<GVamStatus.Query>({
          query: VamStatusQuery,
          variables,
        })
        if (result.data) {
          self.useAgreements(result.data.vamStatus)
        }
      } catch (err) {
        self.vamFailed()
        Sentry.withScope(scope => {
          Object.keys(variables).forEach(key => {
            scope.setExtra(key, variables[key])
          })
          Sentry.captureException(new VError(err, 'VAM init failed'))
        })
      }
    },
    async updateVam(formValues: IOrderFormState) {
      const phone = `${formValues.phonePrefix.trim()}${formValues.phone.trim()}`
      const email = formValues.email.trim()
      const firstname = formValues.firstname.trim()
      const lastname = formValues.lastname.trim()
      const variables: GVamStatus.Variables = {
        input: {
          email,
          phone,
          firstname,
          lastname,
          orderId: self.orderId!,
        },
      }
      try {
        const result = await self.client.query<GVamStatus.Query>({
          query: VamStatusQuery,
          variables,
        })
        if (result.data) {
          self.useAgreements(result.data.vamStatus)
        }
      } catch (err) {
        self.vamFailed()
        Sentry.withScope(scope => {
          Object.keys(variables).forEach(key => {
            scope.setExtra(key, variables[key])
          })
          Sentry.captureException(new VError(err, 'VAM update failed'))
        })
      }
    },
  }))
  .actions(self => ({
    init() {
      Sentry.configureScope(scope => {
        scope.setExtra('orderId', self.orderId)
      })
    },
  }))

export interface TOrderModel extends Instance<typeof OrderModel> {}
export interface TOrderModelProps extends SnapshotIn<typeof OrderModel> {}
